/* ------------------ИМПОРТЫ/IMPORTS----------------- */
@import url('https://fonts.googleapis.com/css2?family=Lilita+One&family=Urbanist:wght@200;400;700;900&display=swap');
/* ------------------ОБНУЛЕНИЕ/RESET------------------ */
* {
    margin: 0;
    padding: 0;
    border: 0;
}

*,
:after,
:before {
    box-sizing: border-box;
}

:focus,
:active {
    outline: none;
}

a:focus,
a:active {
    outline: none;
}

nav,
footer,
header,
aside {
    display: block;
}

html,
body {
    height: 100%;
    width: 100%;
    line-height: 1;
    font-size: 14px;
    -ms-text-size-adjust: 100%;
    -moz-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
    font-family: inherit;
}

input::-ms-clear {
    display: none;
}

button {
    cursor: pointer;
    background-color: transparent;
}

button::-moz-focus-inner {
    padding: 0;
    border: 0;
}

a,
a:visited {
    text-decoration: none;
}

a:hover {
    text-decoration: none;
}

ul li {
    list-style: none;
}

img {
    vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-size: inherit;
    font-weight: 400;
}

/* ------------------СТИЛИ/STYLES------------------ */

body {
    background-color: #232323;
    color: #f1f1f1;
    font-family: 'Urbanist', sans-serif;
    font-weight: 400;
    font-size: 18px;
}

._container {
    max-width: 1440px;
}
